<template>
  <div class="product-page container">
    <router-link to="/" class="btn" role="button">
      <img :src="require(`@/assets/icons/home-icon.png`)" />
    </router-link>
    <h1 class="product-page-title">Eucerin® recommended products:</h1>
    <div v-if="!showPharmacyAdviseText">
      <div class="result-wrapper row">
        <div class="col-md-4" v-for="(item, i) in categories" :key="i">
          <!-- <span class="result-title">{{ item.Title }} </span> -->
          <span class="next-splide">
            <svg
              xmlns="http://www.w3.org/2000/svg"
              id="Layer_1"
              data-name="Layer 1"
              viewBox="0 0 24.5 26.73"
            >
              <defs></defs>
              <g id="Page-1">
                <g id="Resultpage-3">
                  <g id="REINIGUNG">
                    <g id="Group-4">
                      <g id="pfeil">
                        <path
                          id="Stroke-1"
                          class="cls-1"
                          d="M13.24,9.51l6.53-6.29.62-.59a2.44,2.44,0,0,1,3.3,0,2.17,2.17,0,0,1,0,3.2c-1.25,1.23-2.54,2.43-3.81,3.65L15,14.13a2.46,2.46,0,0,1-3.71,0Q7.07,10.06,2.82,6a2.21,2.21,0,0,1-.15-3.3,2.47,2.47,0,0,1,3.51.12L12.63,9C12.8,9.12,13,9.27,13.24,9.51Z"
                          transform="translate(-0.95 -0.95)"
                        />
                        <path
                          id="Stroke-3"
                          class="cls-1"
                          d="M13.24,21.17c1.27-1.23,2.48-2.4,3.7-3.56s2.19-2.11,3.3-3.16a2.45,2.45,0,0,1,3.45-.14,2.2,2.2,0,0,1-.13,3.34C20.7,20.4,17.82,23.14,15,25.88a2.42,2.42,0,0,1-3.5,0q-4.33-4.11-8.63-8.23a2.21,2.21,0,0,1-.11-3.34,2.47,2.47,0,0,1,3.46.14c2.16,2.05,4.3,4.11,6.45,6.16Z"
                          transform="translate(-0.95 -0.95)"
                        />
                      </g>
                    </g>
                  </g>
                </g>
              </g>
            </svg>
          </span>
          <splide :options="sliderOptions" :classes="classesOptions">
            <splide-slide v-for="(Id, i) in item.ProductIds" :key="i">
              <Products
                :product="loadProduct(Id)"
                :productTitle="item.Title"
              ></Products>
            </splide-slide>
          </splide>
          <img class="plus-img" :src="require(`@/assets/icons/plus.png`)" />
        </div>
      </div>
    </div>
    <div class="product-view-text" v-else>
      <div class="shape-bg">
        <div class="result-title">{{ pharmacyAdviseText }}</div>
      </div>
    </div>
    <router-link
      :to="{ name: 'questions', params: { Id: 1 } }"
      class="btn reload-icon"
      role="button"
    >
      <img :src="require(`@/assets/icons/reload-icon.png`)" />
    </router-link>
  </div>
</template>

<script>
import Products from './Product.vue';
// import ProductText from './ProductText.vue';

export default {
  name: 'ProductsView',
  data() {
    return {
      categories: [],
      products: [],
      defaultObject: [],
      pharmacyAdviseText: '',
      showPharmacyAdviseText: false,
      classesOptions: {
        next: 'next-splide',
      },
      sliderOptions: {
        arrows: true,
        type: 'loop',
        direction: 'ttb',
        focus: 'top',
        pagination: false,
        isNavigation: false,
        perPage: 1,
        height: 490,
        width: 400,
      },
    };
  },
  components: {
    Products,
    // ProductText,
  },
  mounted() {
    const products = this.$store.getters.productsFile;
    this.loadData(products);
  },
  methods: {
    loadData(products) {
      const pathToData = `data/${products}`;
      fetch(pathToData)
        .then((response) => response.json())
        .then((data) => this.initData(data))
        .catch((e) => {
          window.console.log('Error fetching product json', e);
        });
    },
    initData(data) {
      this.products = data.data;
      this.loadCategories();
    },
    loadProduct(Id) {
      let result = [];
      if (this.products) {
        result = this.products.filter((x) => x.Id === Id);
      }
      return result;
    },
    sliderInit() {},
    loadCategories() {
      this.categories = this.$store.getters.productList;
      this.showPharmacyAdviseText = this.$store.getters.productList.ShowPharmacyAdviseText;
      this.pharmacyAdviseText = this.$store.getters.productList.PharmacyAdviseText;

      const categoryList = [];
      Object.keys(this.categories).forEach((key) => {
        if (key.includes('Products')) categoryList.push(this.categories[key]);
      });
      this.categories = categoryList;

      if (Object.entries(this.categories).length === 0) {
        this.categories = this.defaultObject;
      }
    },
  },
};
</script>

<style lang="scss">
@import '~@splidejs/splide/dist/css/splide.min.css';

.product-page {
  padding: 50px 0;

  h1 {
    padding-bottom: 7px;
  }
  .router-link-active {
    top: 30px;
  }
}

.next-splide {
  width: 27px;
  height: 27px;
  bottom: 120px;
  fill: var(--DataRangeColorCode);
  position: absolute;
  left: 50%;
  z-index: 99;
  transform: translateX(-50%);

  svg {
    fill: #878c92;
  }
}

.result-wrapper .splide .splide__arrow {
  opacity: 0;
  z-index: 99;
}

.splide__slide:not(.is-active) .next-splide > svg {
  display: none;
}

.result-wrapper {
  justify-content: center;
  margin-bottom: 50px;
  padding: 50px 40px;
  h5 {
    color: #6d7278;
    font-size: 16px;
    font-weight: 400;
    text-align: center;
    height: 39px;
    overflow: hidden;
  }
  .splide__track {
    height: 646px !important;
  }
  .product-description {
    padding-left: 15px;
    padding-right: 15px;
    max-height: 127px;
    overflow: hidden;

    ul {
      list-style-type: none;
      padding-left: 20px;

      li {
        position: relative;
        font-size: 14px;
      }

      li:before {
        content: '';
        display: inline-block;
        transform: rotate(45deg);
        height: 13px;
        width: 7px;
        left: -18px;
        top: 4px;
        border-bottom: 2px solid #333;
        position: absolute;
        border-right: 2px solid #333;
      }
    }
  }
  .result-title {
    border-radius: 10px;
    position: absolute;
    z-index: 9;
    font-size: 24px;
    color: white;
    left: 50%;
    top: 1.5px;
    transform: translateX(-50%);
    text-transform: uppercase;
    padding: 7px 15px;
    text-align: center;
    transition: 0.2s all ease-in-out;
    min-width: 180px;
    width: fit-content;
  }

  .splide {
    position: relative;
    .splide__arrow {
      background: none;
    }

    + .plus-img {
