      background-color: var(--DataRangeColorCode);
    }

    .splide__arrow:before {
      fill: var(--DataRangeColorCode);
    }
  }

  .col-md-4:last-of-type {
    .splide__slide.is-active .result-title {
      background-color: var(--DataRangeColorCode);
    }

    .splide__arrow:before {
      fill: var(--DataRangeColorCode);
    }
  }
}

.result-wrapper .col-md-4:last-of-type .plus-img {
  display: none;
}

.product-teaser {
  + .product-teaser {
    margin-top: 10px;
  }
}
.splide--ttb > .splide__arrows {
  .splide__arrow--prev {
    display: none;
    top: -30px;
  }

  .splide__arrow--next {
    bottom: -30px;
  }
}

.btn:not(.btn-light) {
  padding: 0;
}

.splide__slide .row {
  flex-direction: column;
  margin: 0;
}

