  position: relative;

  .result-title {
    color: #a70532;
    max-width: 720px;
    font-size: 34px;
    line-height: 44px;
    text-align: center;
    text-transform: uppercase;
    left: 50%;
    top: 35%;
    transform: translate(-50%, -50%);
    position: absolute;
    width: 100%;

    &:before,
    &:after {
      content: '';
      background-size: 76px 53px;
      height: 53px;
      position: absolute;
      top: 9px;
      width: 76px;
